.nav-links{
  flex: 1;
  
  width:20px;
  border-radius: 10px;
  border: #00dcd1;

}
.nav-links ul li{
  list-style: none;
  display: inline-block;
  padding: 8px 12px;
  position: relative;
  
  animation: fadeIn 5s;
  -webkit-animation: fadeIn 5s;
  -moz-animation: fadeIn 5s;
  -o-animation: fadeIn 5s;
  -ms-animation: fadeIn 5s;
  
} 
.nav-links ul li a{
  color: #000000;
  text-decoration: none;
  font-size: 14px;
  
  
}
.nav-links ul li::after{
  content: '';
  width: 0%;
  height: 2px;
  background: #00dcd1;
  display: block;
  margin: auto;
}
.nav-links ul li:hover::after{
  width: 100%;
}

